<template>
  
  <div id="app">
    <div id="buttons">
      <button v-on:click="hide('1')" class="menuBtn">Create Notification</button>
      <br>
      <button v-on:click="hide('2')" class="menuBtn">Change Layout</button>
      <br>
      <button v-on:click="hide('3')" class="menuBtn">Schedule Notification</button>
    </div>

    <div v-on:click="hideAll">
    <img src="./assets/delete.png"  id="close">
    </div>
    

    <CreateImage id="createMenu"/>
    <ChangeLayout id="changeMenu"/>
    <SetSchedule id="scheduleMenu"/>
  </div>
</template>

<script>


import CreateImage from "./components/CreateImage.vue";
import ChangeLayout from "./components/ChangeLayout.vue";
import SetSchedule from "./components/SetSchedule.vue";

export default {
  name: "app",
  components: {
    CreateImage,
    ChangeLayout,
    SetSchedule
  },
  methods:{
    hide: function(text){
      let x = document.getElementById("changeMenu");
      if(text==1){
        x = document.getElementById("createMenu")
      }else if(text==2){
        x = document.getElementById("changeMenu")
      }else{
        x = document.getElementById("scheduleMenu")
      }
      
      if (x.style.display == "none") {
        x.style.display = "block";
      } else {
        x.style.display = "none";
      }

      let y = document.getElementById("close");

      if (y.style.display == "none") {
        y.style.display = "block";
      } else {
        y.style.display = "none";
      }
    },
    hideAll: function(){
      let x = document.getElementById("close");

      if (x.style.display == "none") {
        x.style.display = "block";
      } else {
        x.style.display = "none";
      }

      let a = document.getElementById("createMenu");
      let b = document.getElementById("changeMenu");
      let c = document.getElementById("scheduleMenu");

      a.style.display = "none";
      b.style.display = "none";
      c.style.display = "none";
    }
  }
  
};
</script>


